.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #000;
  height: 48px;
}

.flex_container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
.row {
  width: 65%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin: auto;
}
.list {
  color: #fff;
  font-size: 17px;
  font-weight: 500;
  margin: 0;
  text-transform: capitalize;
  cursor: pointer;
}
.active {
  color: #f11133;
}
